.fixture-page-container .stats-container {
    max-width: 500px;
    width: 100%;
}

.fixture-page-container .stats-container .top-row {
    width: 100%;
    border-bottom: 2px solid rgb(255, 255, 255, 0.6);
    color: rgb(255, 255, 255, 0.6);
    font-size: 12px;
    font-weight: 500;
    
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.fixture-page-container .stats-container .row {
    width: 100%;
    height: 45px;
    border-bottom: 1px solid rgb(255, 255, 255, 0.2);

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}

.fixture-page-container .stats-container .row .color {
    height: 55%;
    width: 4px;
}

.fixture-page-container .stats-container .row .color.active {
    background-color: #004D98;
}

.fixture-page-container .stats-container .row .color.home {
    margin-right: 10px;
}

.fixture-page-container .stats-container .row .color.away {
    margin-left: 10px;
}

.fixture-page-container .stats-container .row .value {
    color: white;
    font-weight: 600;
    font-size: 14px;
}

.fixture-page-container .stats-container .row .stat-type {
    margin: auto;
    color: white;
    font-size: 13px;
    font-weight: 500;
}

.fixture-page-container .stats-unavailable {
    text-align: center;
    width: 100%;
    color: rgb(255, 255, 255, 0.7);
    font-weight: 500; 
    font-size: 13px;  
    margin-top: 30px; 
    border-bottom: 1px solid rgb(255, 255, 255, 0.7);
    padding-bottom: 20px;
    max-width: 500px;
}

.fixture-page-container .stats-unavailable i {
    margin-right: 5px;
}

@media (max-width:540px) {
    .fixture-page-container .stats-unavailable {
        width: calc(100% - 20px);
    }
    .fixture-page-container .stats-container {
        width: calc(100% - 20px);
    }
}