.player-standings-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 600px;
    max-width: 100%;
}

.player-standings-container .table-box {
    margin-bottom: 50px;
    width: 100%;
    overflow: hidden;
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.player-standings-container .table-box.less {
    height: 245px;
}

.player-standings-container .table-box.more {
    height: 100%;
}

.player-standings-container .table-box .header {
    width: 100%;
    min-height: 45px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: rgb(255, 255, 255, 0.1);
}

.player-standings-container .table-box .header img {
    height: 30px;
    width: 30px;
    margin-left: 12px;
    margin-right: 3px;
}


.player-standings-container .table-box .header span.league-name {
    font-size: 13px;
    color: white;
    font-weight: 300;
}

.player-standings-container .table-box .header span.title {
    font-size: 13px;
    color: rgb(255, 255, 255, 1);
    margin-left: auto;
    font-weight: 600;
    margin-right: 10px;
}

.player-standings-container .table-box .row {
    width: 100%;
    min-height: 60px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: rgb(255, 255, 255, 0.05);
    padding: 0 15px;
    border-bottom: 1px solid rgb(255, 255, 255, 0.15);
}

.player-standings-container .table-box .row img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin-right: 15px;
}

.player-standings-container .table-box .row .text-box {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.player-standings-container .table-box .row .text-box .top {
    font-size: 13px;
    color: white;
    margin-bottom: 3px;
    font-weight: 400;
}

.player-standings-container .table-box .row .text-box .bottom {
    font-size: 13px;
    color: rgb(255, 255, 255, 0.6);
    font-weight: 200;
    margin-top: 3px;
}

.player-standings-container .table-box .row .value {
    margin-left: auto;
    color: white;
    font-weight: 700;
    font-size: 14px;
}

.player-standings-container .table-box .expand-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    cursor: pointer;
    position: sticky;
    bottom: 0;
    text-transform: uppercase;
    width: 100%;
    min-height: 20px;
    color: rgb(255, 255, 255, 0.7);
    font-size: 11px;
    font-weight: 300;
    background-color: #393942;
}

.player-standings-container .table-box .expand-btn i {
    margin-left: 10px;
    font-size: 14px;
}