.standings-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: calc(100vh - 75px);
    padding-top: 30px;
    overflow-y: auto;
}

.standings-container > a {
    max-width: 500px;
    width: 100%;
}

.standings-container > .loader-container {
    margin: 10px auto 10px;
}

.standings-container::-webkit-scrollbar {
    width: 10px;
}

.standings-container::-webkit-scrollbar-track {
    background: transparent;
}

.standings-container::-webkit-scrollbar-thumb {
    background: rgb(255, 255, 255, 0.1);
}

.standings-container::-webkit-scrollbar-thumb:hover {
    background: rgb(255, 255, 255, 0.3);
}

.standings-container .no-currentdata {
    color: rgb(255, 255, 255, 0.6);
    margin: 10px 0;
    font-size: 13px;
}

.standings-container .no-currentdata i {
    margin-right: 6px;
}

@media (max-width:540px) {
    .standings-container {
        padding-left: 10px;
        padding-right: 10px;
    }
}