.fixture-page-container .formation-box {
    margin-top: 10px;
    margin-bottom: 10px;
    color: rgb(255, 255, 255, 0.4);
    padding: 4px 10px;
    border-radius: 10px;
    max-width: 500px;
}

.fixture-page-container .lineup-unavailable {
    text-align: center;
    width: 100%;
    color: rgb(255, 255, 255, 0.7);
    font-weight: 500; 
    font-size: 13px;  
    margin-top: 30px; 
    border-bottom: 1px solid rgb(255, 255, 255, 0.7);
    padding-bottom: 20px;
    max-width: 500px;
}

.fixture-page-container .lineup-unavailable i {
    margin-right: 5px;
}

@media (max-width:540px) {
    .fixture-page-container .lineup-unavailable {
        width: calc(100% - 20px);
    }
}