.fixture-page-container .other-members-container {
    max-width: 500px;
    width: calc(100% - 30px);
    min-height: fit-content;
    margin-top: 30px;
}

.fixture-page-container .other-members-container > .header {
    background-color: rgb(22, 22, 33);
    height: 40px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    padding-left: 10px;
    padding-bottom: 4px;
    color: rgb(255, 255, 255, 0.4);
    font-size: 13px;
    font-weight: 600;
}

.fixture-page-container .other-members-container > .item {
    background-color: rgb(255, 255, 255, 0.1);
    height: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 10px;
}

.fixture-page-container .other-members-container > .item {
    border-bottom: 1px solid rgb(255, 255, 255, 0.2);
}

.fixture-page-container .other-members-container > .item:last-of-type {
    border-bottom: none;
}

.fixture-page-container .other-members-container > .item > img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin-right: 10px;
}

.fixture-page-container .other-members-container > .item > .number {
    margin-right: 10px;
    color: rgb(255, 255, 255, 0.7);
    font-weight: 600;
    font-size: 12px;
    width: 15px;
}

.fixture-page-container .other-members-container > .item > span {
    color: rgb(255, 255, 255, 0.8);
    font-size: 14px;
    font-weight: 400;
}

.fixture-page-container .other-members-container > .item.subs > span {
    font-weight: 600;
}

.fixture-page-container .other-members-container > .item.bench > span > span {
    color: rgb(255, 255, 255, 0.4);
    font-size: 11px;
    font-weight: 300;
    /* font-weight: 600; */
}

.fixture-page-container .other-members-container > .item.subs > .rating-box {
    width: 35px;
    height: 25px;
    border-radius: 20px;
    font-size: 12px;
    color: white;
    margin-right: 15px;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.fixture-page-container .other-members-container > .item.subs > .time {
    margin-left: auto;
    font-size: 12px;
    color: white;
    font-weight: 600;
    margin-right: 10px;
}

.fixture-page-container .other-members-container > .item.subs > .time > i {
    font-size: 9px;
    color: rgb(255, 255, 255, 0.4);
}

.fixture-page-container .other-members-container > .item.subs {
    height: 60px;
}

.fixture-page-container .other-members-container > .item.subs > .top-bottom-box {
    height: 100%;
    padding: 8px 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.fixture-page-container .other-members-container > .item.subs > .top-bottom-box .top, 
.fixture-page-container .other-members-container > .item.subs > .top-bottom-box .bottom {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 50%;
}

.fixture-page-container .other-members-container > .item.subs .number {
    margin-right: 10px;
    color: rgb(255, 255, 255, 0.7);
    font-weight: 600;
    font-size: 12px;
    width: 15px;
}

.fixture-page-container .other-members-container > .item.subs span {
    color: rgb(255, 255, 255, 0.8);
    font-size: 13px;
    font-weight: 500;
}

.fixture-page-container .other-members-container > .item.subs > .top-bottom-box .top span {
    color: rgb(0, 200, 0, 0.8);
}

.fixture-page-container .other-members-container > .item.subs > .top-bottom-box .bottom span {
    color: rgb(255, 0, 0, 0.9);
}

.fixture-page-container .other-members-container > .item.subs > .rating-box.none  {
    background-color: #8f8f8f; 
    /* <= 8.0 */
}

.fixture-page-container .other-members-container > .item.subs > .rating-box.good  {
    background-color: #00A3FF; 
    /* <= 8.0 */
}

.fixture-page-container .other-members-container > .item.subs > .rating-box.avg  {
    background-color: #40A130;
    /* <= 7.0 */
}

.fixture-page-container .other-members-container > .item.subs > .rating-box.below-avg  {
    background-color: #d89b00;
    /* <= 6.0 */
}

.fixture-page-container .other-members-container > .item.subs > .rating-box.bad  {
    background-color: #FF3C3C;
    /* <= 5.0 */
}