.fixture-page-container .players-container {
    margin-top: 30px;
    max-width: 500px;
    width: 100%;
    height: fit-content;
}

/* Player Box */

.fixture-page-container .player-box {
    width: 100%;
    overflow: hidden;
    border-bottom: 1px solid rgb(255, 255, 255, 0.3);
    transition: height 0.5s;
}

.fixture-page-container .player-box.closed {
    height: 50px;
}

.fixture-page-container .player-box.small.opened {
    height: 405px;
}

.fixture-page-container .player-box.large.opened {
    height: 604px;
}

/* Top Area */

.fixture-page-container .player-box .top-area {
    height: 50px;
    background-color: rgb(255, 255, 255, 0.07);
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    cursor: pointer;
}

.fixture-page-container .player-box .top-area img.player-photo {
    width: 35px;
    height: 35px;
    border-radius: 50%;
    margin: 0 10px;
}

.fixture-page-container .player-box .top-area .player-number {
    color: white;
    margin-right: 10px;
    font-size: 13px;
    font-weight: 600;
} 

.fixture-page-container .player-box .top-area .player-name {
    color: white;
    font-size: 13px;
    font-weight: 400;
} 

.fixture-page-container .player-box .top-area .player-captain {
    color: #EDBB00;
    font-size: 12px;
    font-weight: 300;
    margin-left: 5px;
} 

.fixture-page-container .player-box .top-area .player-position {
    margin-left: auto;
    color: rgb(255, 255, 255, 0.8);
    font-size: 12px;
    font-weight: 300;
} 

.fixture-page-container .player-box .top-area i.arrow-icon {
    margin: 0 10px;
    color: white;
    font-size: 15px;
    transition: transform 0.5s;
}

.fixture-page-container .player-box.closed .top-area i.arrow-icon {
    transform: rotate(0);
}

.fixture-page-container .player-box.opened .top-area i.arrow-icon {
    transform: rotate(-180deg);
}

/* Bottom Area */

.fixture-page-container .player-box .bottom-area {
    overflow: hidden;
    display: flex;
    background-color: rgb(255, 255, 255, 0.02);
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px 5px;
}

.fixture-page-container .player-box .group {
    width: 50%;
    height: fit-content;
    padding: 10px;
}

.fixture-page-container .player-box .group .header {
    font-size: 13px;
    font-weight: 700;
    margin-bottom: 10px;
    color: #42A2FF;
    text-transform: capitalize;
}

.fixture-page-container .player-box .group .row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 5px;
    color: rgb(255, 255, 255, 0.8);
    font-size: 13px;
    font-weight: 400;
}

.fixture-page-container .player-box .group .row .key {
    text-transform: capitalize;
}

.fixture-page-container .player-box .group .row .value.good {
    color: rgb(0, 200, 0);
}

.fixture-page-container .player-box .group .row .value.avg {
    color: rgb(255, 196, 0);
}

.fixture-page-container .player-box .group .row .value.bad {
    color: rgb(255, 0, 0);
}

/* Players Unavailable */

.fixture-page-container .players-unavailable {
    text-align: center;
    width: 100%;
    color: rgb(255, 255, 255, 0.7);
    font-weight: 500; 
    font-size: 13px;  
    margin-top: 30px; 
    border-bottom: 1px solid rgb(255, 255, 255, 0.7);
    padding-bottom: 20px;
    max-width: 500px;
}

.fixture-page-container .players-unavailable i {
    margin-right: 5px;
}

@media (max-width:540px) {
    .fixture-page-container .players-unavailable {
        width: calc(100% - 20px);
    }
    .fixture-page-container .players-container {
        width: calc(100% - 20px);
    }
}