nav.bottom-nav {
    position: fixed;
    bottom: 0;
    background-color: #22222c;
    width: 100%;
    height: 75px;

    display: flex;
    justify-content: center;
    align-items: center;
}

nav.bottom-nav .item-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #616169;
    margin-right: 25px;
    text-decoration: none;
}

nav.bottom-nav .item-container i {
    font-size: 25px;
}

nav.bottom-nav .item-container span {
    color: inherit;
    margin-top: 7px;
    font-weight: 300;
    font-size: 13px;
}