.fixture-specific-top-area .top-nav, .standings-container .top-nav {
    width: 100%;
    min-height: 40px;
    height: 40px;
    margin: 15px 0 40px;

    display: flex;
    justify-content: center;
    align-items: center;
}

.fixture-specific-top-area .top-nav div, .standings-container .top-nav div {
    height: 100%;
    width: 100px;
    border: 1px solid rgb(255, 255, 255, 0.8);
    color: white;
    margin-right: 10px;
    font-size: 14px;
    font-weight: 300;
    cursor: pointer;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.fixture-specific-top-area .top-nav div:last-of-type, .standings-container .top-nav div:last-of-type {
    margin-right: 0;
}

.fixture-specific-top-area .top-nav div.selected, .standings-container .top-nav div.selected {
    background-color: #004D98;
    border-color: #004D98;
    cursor: default;
}

@media (max-width:540px) {
    .fixture-specific-top-area .top-nav div, .standings-container .top-nav div {
        width: 80px;
        font-size: 13px;
    }
}