.fixture-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: calc(100vh - 75px);
    padding-top: 30px;
    overflow-y: auto;
}

.fixture-container > a {
    max-width: 500px;
    width: 100%;
}

.fixture-container > .loader-container {
    margin: 10px auto 10px;
}

.fixture-container::-webkit-scrollbar {
    width: 10px;
}

.fixture-container::-webkit-scrollbar-track {
    background: transparent;
}

.fixture-container::-webkit-scrollbar-thumb {
    background: rgb(255, 255, 255, 0.1);
}

.fixture-container::-webkit-scrollbar-thumb:hover {
    background: rgb(255, 255, 255, 0.3);
}

@media (max-width:540px) {
    .fixture-container {
        padding-left: 10px;
        padding-right: 10px;
    }
}