@keyframes blink {
    0% {
        opacity: 100%;
    }
    50% {
        opacity: 20%;
    }
    100% {
        opacity: 100%;
    }
}

@keyframes line-sliding {
    0% {
        width: 0%;
        left: 0;
    }
    50% {
        width: 100%;
        left: 0;
    }
    100% {
        width: 0%;
        left: 100%;
    }
}

.fixture-box-present {
    border-width: 1px;
    border-color: rgb(255, 255, 255, 0.4);
    border-style: solid;
    width: 500px;
    max-width: 100%;
    border-radius: 10px;
    overflow: hidden;
    margin: 0 auto 30px;
    min-height: 215px;
    background-size: 100% !important;
    background-position-y: 100% !important;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}

/* Top Area */

.fixture-box-present > .top-area {
    min-height: 45px;
    border-bottom-width: 1px;
    border-bottom-color: rgb(255, 255, 255, 0.4);
    border-bottom-style: solid;
    color: white;
    padding: 0 12px;

    display: flex;
    align-items: center;
}

.fixture-box-present > .top-area > img.league-logo {
    width: 30px;
    height: 30px;
    background-color: rgb(255, 255, 255, 0.05);
    padding: 2px;
    border-radius: 5px;
}

.fixture-box-present > .top-area > span.league-name {
    margin-left: 6px;
    font-size: 12px;
    font-weight: 700;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-right: 10px;
}

.fixture-box-present > .top-area > span.live-icon {
    margin-left: auto;
    font-size: 12px;
    font-weight: 500;
    transform: translateY(-3px);

    animation-name: blink;
    animation-duration: 2s;
    animation-iteration-count: infinite;
} 

.fixture-box-present > .top-area > span.live-icon > span {
    color: red;
    font-size: 20px;
    margin-right: 3px;
}

/* Main Area */

.fixture-box-present > .main-area, .top-scoreboard.present > .main-area {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    min-height: 170px;
    padding: 0 20px;
}

.fixture-box-present > .main-area > .time, .top-scoreboard.present > .main-area > .time {
    display: flex;
    flex-direction: column;
    font-size: 45px;
    font-weight: 300;
    color: rgb(255, 255, 255, 0.5);
}

.fixture-box-present > .main-area > .separator, .top-scoreboard.present > .main-area > .separator {
    width: 3px;
    height: 100px;
    background-color: rgb(255, 255, 255, 0.3);
    margin: 0 20px;
}

.fixture-box-present > .main-area > .team-container, .top-scoreboard.present > .main-area > .team-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 150px;
}

.fixture-box-present > .main-area > .team-container > .team-logo, .top-scoreboard.present > .main-area > .team-container > .team-logo {
    width: 70px;
    height: 70px;
}

.fixture-box-present > .main-area > .team-container > .team-name, .top-scoreboard.present > .main-area > .team-container > .team-name {
    color: rgb(255, 255, 255, 1);
    font-size: 14px;
    font-weight: 400;
    margin: 7px 20px 0;
    text-align: center;
    overflow: hidden;
}

.fixture-box-present > .main-area > .scoreboard > div, .top-scoreboard.present > .main-area > .scoreboard > div {
    display: flex;
    flex-direction: row;
}

.fixture-box-present > .main-area > .scoreboard > .time-elapsed, .top-scoreboard.present > .main-area > .scoreboard > .time-elapsed {
    position: relative;
    display: block;
    color: #FFED02;
    margin: auto;
    font-weight: 500;
    font-size: 14px;
    transform: translateY(10px);
    width: fit-content;
}

.fixture-box-present > .main-area > .scoreboard > .time-elapsed > .line, .top-scoreboard.present > .main-area > .scoreboard > .time-elapsed > .line {
    position: absolute;
    transform: translateY(2px);
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #FFED02;

    animation-name: line-sliding;
    animation-duration: 2s;
    animation-iteration-count: infinite;
}

.fixture-box-present > .main-area > .scoreboard i, .top-scoreboard.present > .main-area > .scoreboard i {
    color: rgb(255, 255, 255, 1);
    font-size: 20px;
    margin: 0 10px;
}

.fixture-box-present > .main-area > .scoreboard .score, .top-scoreboard.present > .main-area > .scoreboard .score {
    color: rgb(255, 255, 255, 1);
    font-weight: 800;
    font-size: 20px;
    margin: 0 20px 0;
}

@media (max-width:540px) {
    .fixture-box-present {
        min-height: 175px;
        margin-bottom: 20px;
        background-size: 120% !important;
        background-position-x: 50% !important;
    }
    .fixture-box-present > .main-area, .top-scoreboard.present > .main-area {
        padding-left: 10px;
        padding-right: 10px;
        justify-content: center;
        min-height: 130px;
    }
    .fixture-box-present > .main-area > .team-container, .top-scoreboard.present > .main-area > .team-container {
        width: 40%;
    }
    .fixture-box-present > .main-area > .team-container > .team-logo, .top-scoreboard.present > .main-area > .team-container > .team-logo {
        width: 50px;
        height: 50px;
    }
    .fixture-box-present > .main-area > .team-container > .team-name, .top-scoreboard.present > .main-area > .team-container > .team-name {
        font-size: 12px;
    }
    .fixture-box-present > .main-area > .scoreboard > .description, .top-scoreboard.present > .main-area > .scoreboard > .description {
        font-size: 12px;
    }
    .fixture-box-present > .top-area > span.league-name {
        width: 40%;
    }
}