.fixture-page-container .choose-team {
    border: 1px solid rgb(255, 255, 255, 0.7);
    min-height: 40px;
    border-radius: 20px;
    overflow: hidden;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
}

.choose-team > .item {
    padding: 0 20px;
    text-align: center;
    background-color: transparent;
    color: rgb(255, 255, 255, 1);
    font-size: 13px;
    font-weight: 400;
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;
}

.choose-team > .item.selected {
    background-color: rgb(255, 255, 255, 0.9);
    color: rgb(22, 22, 33);
}