.fixture-box-future {
    border-width: 1px;
    border-color: rgb(255, 255, 255, 0.4);
    border-style: solid;
    width: 500px;
    max-width: 100%;
    border-radius: 10px;
    overflow: hidden;
    margin: 0 auto 30px;
    min-height: 215px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}

/* Top Area */

.fixture-box-future > .top-area {
    min-height: 45px;
    border-bottom-width: 1px;
    border-bottom-color: rgb(255, 255, 255, 0.4);
    border-bottom-style: solid;
    color: white;
    padding: 0 12px;

    display: flex;
    align-items: center;
}

.fixture-box-future > .top-area > img.league-logo {
    width: 30px;
    height: 30px;
    background-color: rgb(255, 255, 255, 0.05);
    padding: 2px;
    border-radius: 5px;
}

.fixture-box-future > .top-area > span.league-name {
    margin-left: 6px;
    font-size: 12px;
    font-weight: 700;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-right: 10px;
}

.fixture-box-future > .top-area > span.date {
    margin-left: auto;
    font-size: 12px;
    font-weight: 500;
} 

.fixture-box-future > .top-area > span.date > span.time-responsive {
    display: none;
    color: #FFED02;
    font-weight: 500;
    opacity: 0.8;
} 

/* Main Area */

.fixture-box-future > .main-area, .top-scoreboard.future > .main-area {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    min-height: 170px;
    padding-left: 30px;
    padding-right: 10px;
}

.fixture-box-future > .main-area > .time, .top-scoreboard.future > .main-area > .time {
    display: flex;
    flex-direction: column;
    font-size: 45px;
    font-weight: 500;
    color: rgb(255, 255, 255, 1);
}

.fixture-box-future > .main-area > .separator, .top-scoreboard.future > .main-area > .separator {
    width: 3px;
    height: 100px;
    background-color: rgb(255, 255, 255, 0.3);
    margin: 0 20px;
}

.fixture-box-future > .main-area > .team-container, .top-scoreboard.future > .main-area > .team-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 134px;
}

.fixture-box-future > .main-area > .team-container > .team-logo, .top-scoreboard.future > .main-area > .team-container > .team-logo {
    width: 70px;
    height: 70px;
}

.fixture-box-future > .main-area > .team-container > .team-name, .top-scoreboard.future > .main-area > .team-container > .team-name {
    color: rgb(255, 255, 255, 1);
    font-size: 14px;
    font-weight: 400;
    margin: 7px 20px 0;
    text-align: center;
    overflow: hidden;
}

.fixture-box-future > .main-area > .scoreboard > div, .top-scoreboard.future > .main-area > .scoreboard > div {
    display: flex;
    flex-direction: row;
}
.fixture-box-future > .main-area > .scoreboard .clash-symbol, .top-scoreboard.future > .main-area > .scoreboard .clash-symbol {
    color: rgb(255, 255, 255, 1);
    font-weight: 800;
    font-size: 25px;
    margin: 0 30px 0;
}

@media (max-width:540px) {
    .fixture-box-future {
        min-height: 175px;
        margin-bottom: 20px;
    }
    .fixture-box-future > .main-area, .fixture-box-future > .main-area {
        padding-left: 10px;
        padding-right: 10px;
        justify-content: center;
        min-height: 130px;
    }
    .fixture-box-future > .top-area > span.date > span.time-responsive, .top-scoreboard.future > .top-area > span.date > span.time-responsive {
        display: inline;
    }
    .fixture-box-future > .main-area > .time, .top-scoreboard.future > .main-area > .time {
        display: none;
    }
    .fixture-box-future > .main-area > .separator,  .top-scoreboard.future > .main-area > .separator {
        display: none;
    }
    .fixture-box-future > .main-area > .team-container, .top-scoreboard.future > .main-area > .team-container {
        width: 40%;
    }
    .fixture-box-future > .main-area > .team-container > .team-logo, .top-scoreboard.future > .main-area > .team-container > .team-logo {
        width: 50px;
        height: 50px;
    }
    .fixture-box-future > .main-area > .team-container > .team-name, .top-scoreboard.future > .main-area > .team-container > .team-name {
        font-size: 12px;
    }
    .fixture-box-future > .main-area > .scoreboard > .description, .top-scoreboard.future > .main-area > .scoreboard > .description {
        font-size: 12px;
    }
    .fixture-box-future > .top-area > span.league-name {
        width: 40%;
    }
}