.fixture-page-container {
    height: calc(100vh - 75px);
    width: 100%;
    overflow-y: auto;
    overflow-x: hidden;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}


.fixture-page-container .extra-info-container {
    max-width: 500px;
    width: 100%;
    margin: 40px auto 0;
    padding: 10px 0;
    border-top: 1px solid rgb(255, 255, 255, 0.7);
}

.fixture-page-container .extra-info-container p {
    color: white;
    font-size: 12px;
    font-weight: 400;
}

.fixture-page-container .extra-info-container p span {
    color: rgb(255, 255, 255, 0.6);
    font-weight: 200;
}

.fixture-page-container .recent-info-container, .fixture-page-container .h2h-info-container {
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
}

.fixture-page-container .recent-info-container .header, .fixture-page-container .h2h-info-container .header {
    color: rgb(255, 255, 255, 0.8);
    font-size: 13px;
    font-weight: 500;
    padding: 20px 0 10px;
}

.fixture-page-container .recent-info-container .content {
    width: 100%;
    background-color: rgb(255, 255, 255, 0.05);
    height: 60px;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
}

.fixture-page-container .recent-info-container .content .side {
    width: calc(50% - 5px);

    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.fixture-page-container .recent-info-container .content .side.home {
    padding-right: 20px;
    flex-direction: row-reverse;
}

.fixture-page-container .recent-info-container .content .side.away {
    padding-left: 20px;
    flex-direction: row;
}

.fixture-page-container .recent-info-container .content .side .form {
    min-width: 25px;
    min-height: 25px;
    border-radius: 50%;
    color: white;
    margin: 0 3px;
    font-size: 13px;
    font-weight: 700;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.fixture-page-container .recent-info-container .content .side.home .form:nth-of-type(1),
.fixture-page-container .recent-info-container .content .side.away .form:nth-of-type(1) {
    min-width: 30px;
    min-height: 30px;
}

.fixture-page-container .recent-info-container .content .side.home .form:nth-of-type(2),
.fixture-page-container .recent-info-container .content .side.away .form:nth-of-type(2) {
    min-width: 28px;
    min-height: 28px;
}

.fixture-page-container .recent-info-container .content .side.home .form:nth-of-type(3),
.fixture-page-container .recent-info-container .content .side.away .form:nth-of-type(3) {
    min-width: 26px;
    min-height: 26px;
}

.fixture-page-container .recent-info-container .content .side.home .form:nth-of-type(4),
.fixture-page-container .recent-info-container .content .side.away .form:nth-of-type(4) {
    min-width: 24px;
    min-height: 24px;
}

.fixture-page-container .recent-info-container .content .side.home .form:nth-of-type(5),
.fixture-page-container .recent-info-container .content .side.away .form:nth-of-type(5) {
    min-width: 22px;
    min-height: 22px;
}

.fixture-page-container .recent-info-container .content .side .form.W {
    background-color: rgb(0, 128, 0);
}

.fixture-page-container .recent-info-container .content .side .form.D {
    background-color: rgb(106, 106, 106);
}

.fixture-page-container .recent-info-container .content .side .form.L {
    background-color: rgb(190, 0, 0);
}

.fixture-page-container .recent-info-container .content .separator {
    height: 100%;
    color: white;
    font-size: 20px;
    font-weight: 600;

    display: flex;
    justify-content: center;
    align-items: center;
}

.fixture-page-container .h2h-info-container .content {
    width: 100%;
    background-color: rgb(255, 255, 255, 0.05);
    height: fit-content;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}

.fixture-page-container .h2h-info-container .content .fixture {
    height: 60px;
    border-bottom: 1px solid rgb(255, 255, 255, 0.2);

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.fixture-page-container .h2h-info-container .content .fixture .logo {
    width: 35px;
    height: 35px;
    margin: auto;
}

.fixture-page-container .h2h-info-container .content .fixture .score {
    color: rgb(255, 255, 255, 1);
    font-weight: 600;
    font-size: 18px;
    width: 50px;
    text-align: center;
}

.fixture-page-container .h2h-info-container .content .fixture .date {
    color: rgb(255, 255, 255, 0.4);
    font-size: 12px;
    font-weight: 300;
    margin: 0 10px;
    width: 150px;
    text-align: center;
}

.fixture-page-container .h2h-info-container .content .fixture .date span {
    color: rgb(255, 255, 255, 0.6)
}

@media (max-width:540px) {
    .fixture-page-container .stats-unavailable,
    .fixture-page-container .extra-info-container,
    .fixture-page-container .recent-info-container,
    .fixture-page-container .h2h-info-container {
        width: calc(100% - 20px);
    }
    .fixture-page-container .recent-info-container .content .side.home {
        padding-right: 6px;
    }
    .fixture-page-container .recent-info-container .content .side.away {
        padding-left: 6px;
    }
}