.lineup-container {
    width: 500px;
    min-height: 295px;
    /* background-color: red; */
    background: linear-gradient(0deg, rgba(22, 22, 33, 0.5), rgba(22, 22, 33, 0.5)), url("../../../images/lineup.png");
    background-size: 100%;
    background-repeat: no-repeat;
    padding-bottom: 20px;

    display: flex;
    flex-direction: column-reverse;
    justify-content: flex-start;
    align-items: center;
}

.lineup-container > .row-container {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
    align-items: stretch;
}

.lineup-container.rows-3 > .row-container {
    height: 33.33%
}

.lineup-container.rows-4 > .row-container {
    height: 25%;
}

.lineup-container.rows-5 > .row-container {
    height: 20%;
}

.lineup-container.rows-5 > .row-container.row-1 {
    width: 90%;
}

.lineup-container.rows-5 > .row-container.row-2 {
    width: 85%;
}

.lineup-container.rows-5 > .row-container.row-3 {
    width: 78%;
}

.lineup-container.rows-5 > .row-container.row-4 {
    width: 72%;
}

.lineup-container.rows-5 > .row-container.row-5 {
    width: 65%;
}

.lineup-container.rows-4 > .row-container.row-1 {
    width: 90%;
}

.lineup-container.rows-4 > .row-container.row-2 {
    width: 82%;
}

.lineup-container.rows-4 > .row-container.row-3 {
    width: 74%;
}

.lineup-container.rows-4 > .row-container.row-4 {
    width: 67%;
}

.lineup-container > .row-container > .player-container {
    height: 100%;
    position: relative;
}

.lineup-container > .row-container.count-5 > .player-container {
    width: 20%;
}

.lineup-container > .row-container.count-4 > .player-container {
    width: 25%;
}

.lineup-container > .row-container.count-3 > .player-container {
    width: 33.33%;
}

.lineup-container > .row-container.count-2 > .player-container {
    width: 50%;
}

.lineup-container > .row-container.count-1 > .player-container {
    width: 100%;
}

.lineup-container > .row-container > .player-container > .player-img {
    position: absolute;
    left: calc(50% - 16.5px);
    top: calc(50% - 20px);
    width: 33px;
    height: 33px;
    border-radius: 50%;
    background-color: white;
}

.lineup-container > .row-container > .player-container > .player-img  > img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.lineup-container > .row-container > .player-container > .player-name {
    position: absolute;
    left: 0;
    bottom: 0;
    text-align: center;
    width: 100%;
    height: 10px;
    color: white;
    font-size: 9px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.lineup-container > .row-container > .player-container > .player-number {
    position: absolute;
    left: calc(50% - 25px);
    background-color: white;
    border: 1px solid rgb(0, 0, 0, 0.6);
    width: 17px;
    height: 17px;
    border-radius: 50%;
    font-size: 9px;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.lineup-container.rows-4 > .row-container > .player-container > .player-number {
    top: 10px;
}

.lineup-container.rows-5 > .row-container > .player-container > .player-number {
    top: 2px;
}

.lineup-container > .row-container > .player-container > .player-rating {
    position: absolute;
    left: calc(50% - 25px);
    background-color: green;
    width: 25px;
    height: 15px;
    border-radius: 10px;
    font-size: 9px;
    color: white;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.lineup-container.rows-4 > .row-container > .player-container > .player-rating {
    bottom: 13px;
}

.lineup-container.rows-5 > .row-container > .player-container > .player-rating {
    bottom: 10px;
}

.lineup-container > .row-container > .player-container.good > .player-rating  {
    background-color: #00A3FF; 
    /* <= 8.0 */
}

.lineup-container > .row-container > .player-container.avg > .player-rating  {
    background-color: #40A130;
    /* <= 7.0 */
}

.lineup-container > .row-container > .player-container.below-avg > .player-rating  {
    background-color: #d89b00;
    /* <= 6.0 */
}

.lineup-container > .row-container > .player-container.bad > .player-rating  {
    background-color: #FF3C3C;
    /* <= 5.0 */
}