@keyframes loader-blink {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

.loader-container {
    display: flex;
    flex-direction: column;
    width: fit-content;
    height: fit-content;
    margin: 10px 0;
}

.loader-container div {
    display: flex;
    flex-direction: row;
    width: fit-content;
    height: fit-content;
}

.loader-container .rec {
    width: 10px;
    height: 20px;
    background-color: rgb(255, 255, 255, 0.5);
    margin-right: 2px;
    animation-name: loader-blink;
    animation-duration: 0.7s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

.loader-container .text {
    margin: auto;
    margin-top: 10px;
}

.loader-container .char {
    font-family: 200;
    font-size: 10px;
    color: rgb(255, 255, 255, 0.5);
    animation-name: loader-blink;
    animation-duration: 0.7s;
    animation-fill-mode: forwards;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

.loader-container .char {
    margin-right: 2px;
}

.loader-container .rec:nth-of-type(1), .loader-container .char:nth-of-type(1) {
    animation-delay: 0.1s;
}
.loader-container .rec:nth-of-type(2), .loader-container .char:nth-of-type(2) {
    animation-delay: 0.2s;
}
.loader-container .rec:nth-of-type(3), .loader-container .char:nth-of-type(3) {
    animation-delay: 0.3s;
}
.loader-container .rec:nth-of-type(4), .loader-container .char:nth-of-type(4) {
    animation-delay: 0.4s;
}
.loader-container .rec:nth-of-type(5), .loader-container .char:nth-of-type(5) {
    animation-delay: 0.5s;
}
.loader-container .rec:nth-of-type(6), .loader-container .char:nth-of-type(6) {
    animation-delay: 0.6s;
}
.loader-container .rec:nth-of-type(7), .loader-container .char:nth-of-type(7) {
    animation-delay: 0.7s;
}