.team-standings-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 600px;
    max-width: 100%;
}

.team-standings-container .table-box {
    margin-bottom: 50px;
    overflow-x: auto;
    width: 100%;
}

.team-standings-container .table-box::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}

.team-standings-container .table-box::-webkit-scrollbar-track {
    background: transparent;
}

.team-standings-container .table-box::-webkit-scrollbar-thumb {
    background: rgb(255, 255, 255, 0.1);
}

.team-standings-container .table-box::-webkit-scrollbar-thumb:hover {
    background: rgb(255, 255, 255, 0.3);
}


.team-standings-container .table-box .header {
    width: 600px;
    height: 45px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: rgb(255, 255, 255, 0.05);
    position: relative;
}

.team-standings-container .table-box .header div.image {
    width: 45px;
    position: sticky;
    left: 5px;
    background-color: #22222c;

    display: flex;
    justify-content: center;
    align-items: center;
}

.team-standings-container .table-box .header img {
    height: 30px;
    width: 30px;
}

.team-standings-container .table-box .header span.league-name {
    font-size: 13px;
    color: white;
    font-weight: 300;
    position: sticky;
    left: 45px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.team-standings-container .table-box .header span.group-name {
    font-size: 13px;
    color: rgb(255, 255, 255, 0.6);
    margin-left: auto;
    font-weight: 300;
    position: sticky;
    right: 10px;
}

.team-standings-container .table-box .top-row {
    background-color: rgb(255, 255, 255, 0.15);
    height: 25px;
    width: 600px;
    position: relative;

    display: grid;
    justify-content: flex-end;
    align-items: center;
    grid-template-columns: calc(100% - 330px) 30px 40px 40px 40px 30px 30px 30px 90px;
}

.team-standings-container .table-box .top-row div:first-of-type {
    width: 70px;
    position: sticky;
    background-color: #393942;
    left: 0;
    height: 100%;
}

.team-standings-container .table-box .top-row div {
    text-align: center;
    color: rgb(255, 255, 255, 0.6);
    font-size: 12px;
}

.team-standings-container .table-box .row {
    background-color: rgb(255, 255, 255, 0.05);
    height: 45px;
    border-bottom: 1px solid rgb(255, 255, 255, 0.15);
    width: 600px;
    position: relative;

    display: grid;
    justify-content: flex-end;
    align-items: center;
    grid-template-columns: 30px 40px calc(100% - 400px) 30px 40px 40px 40px 30px 30px 30px 90px;
}

.team-standings-container .table-box .row div.place {
    position: sticky;
    left: -1px;
    background-color: #22222c;
} 

.team-standings-container .table-box .row div.image {
    position: sticky;
    left: 29px;
    background-color: #22222c;
    width: 40px;
    height: calc(100% - 1px);

    display: flex;
    justify-content: center;
    align-items: center;
}

.team-standings-container .table-box .row div.numeric {
    text-align: center;
    color: rgb(255, 255, 255, 1);
    font-size: 13px;
}

.team-standings-container .table-box .row img {
    width: 30px;
    height: 30px;
}

.team-standings-container .table-box .row .team-name {
    text-align: left;
    color: rgb(255, 255, 255, 1);
    font-size: 13px;
}

.team-standings-container .table-box .row .form {
    text-align: center;
    color: rgb(255, 255, 255, 1);
    font-size: 13px;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.team-standings-container .table-box .row .form .form-single {
    text-align: center;
    color: rgb(255, 255, 255, 1);
    font-size: 13px;
    margin: 0 1.2px;
    font-weight: 500;
}

.team-standings-container .table-box .row .form .form-single.W {
    color: rgb(0, 150, 0);   
}

.team-standings-container .table-box .row .form .form-single.D {
    color: rgb(255, 255, 255, 0.5);   
}

.team-standings-container .table-box .row .form .form-single.L {
    color: rgb(200, 0, 0);   
}