.league-selector-container {
    min-height: 50px;
    margin-bottom: 20px;

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: stretch;
}

/* Select League */

.league-selector-container .league-box {
    height: 100%;
    width: 200px;
    margin-right: 12px;
    position: relative;
    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    outline: none !important;
}

.league-selector-container .league-box .visible-area {
    height: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 10px;
    background-color: rgb(255, 255, 255, 0.15);
}

.league-selector-container .league-box .visible-area img, .league-selector-container .league-box .dropdown-menu .dropdown-item img {
    width: 30px;
    height: 30px;
}

.league-selector-container .league-box .visible-area span, .league-selector-container .league-box .dropdown-menu .dropdown-item span {
    color: rgb(255, 255, 255, 1);
    font-weight: 400;
    font-size: 14px;
    margin-left: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.league-selector-container .league-box .visible-area i {
    margin-left: auto;
    margin-right: 5px;
    color: rgb(255, 255, 255, 0.7);
    cursor: pointer;
}

.league-selector-container .league-box .dropdown-menu {
    position: absolute;
    z-index: 1;
    top: 100%;
    left: 0;

    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
    background-color: #22222c;
}

.league-selector-container .league-box .dropdown-menu .dropdown-item {
    padding: 0 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 50px;
    border-top: 1px solid rgb(255, 255, 255, 0.25);
    user-select: none;
}

.league-selector-container .league-box.close .dropdown-menu {
    display: none;
}

.league-selector-container .league-box.open .dropdown-menu {
    display: flex;
}

/* Select Seasons */

.league-selector-container .season-box {
    background-color: rgb(255, 255, 255, 0.15);
    height: 100%;
    margin-right: 12px;

    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.league-selector-container .season-box .left-area, .league-selector-container .season-box .right-area {
    width: 25px;
    height: 100%;
    color: rgb(255, 255, 255, 0.7);
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;
}

.league-selector-container .season-box span {
    color: rgb(255, 255, 255, 1);
    font-weight: 400;
    font-size: 14px;
    width: 50px;
    text-align: center;
}

.league-selector-container .season-box .left-area:hover, .league-selector-container .season-box .right-area:hover {
    background-color: rgb(255, 255, 255, 0.1);
}

/* Search Button */

.league-selector-container .search-box {
    width: 50px;
    height: 100%;
    color: white;
    font-size: 20px;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.league-selector-container .search-box.enabled {
    background-color: #004D98;
    cursor: pointer;
}

.league-selector-container .search-box.disabled {
    background-color: rgb(255, 255, 255, 0.15);
    opacity: 0.5;
}

@media (max-width:500px) {
    .league-selector-container .league-box {
        margin-right: 0px;
        border-right: 1px solid rgb(255, 255, 255, 0.6);
    }
    .league-selector-container .season-box {
        margin-right: 0px;
        border-right: 1px solid rgb(255, 255, 255, 0.6);
    }
}