.fixture-page-container {
    height: calc(100vh - 75px);
    width: 100%;
    overflow-y: auto;
}

.fixture-page-container .empty-events {
    text-align: center;
    width: 100%;
    color: rgb(255, 255, 255, 0.7);
    font-weight: 500; 
    font-size: 13px;  
    margin-top: 30px; 
    border-bottom: 1px solid rgb(255, 255, 255, 0.7);
    padding-bottom: 20px;
    max-width: 500px; 
}

.fixture-page-container .empty-events i {
    margin-right: 5px;
}

.fixture-page-container .events-container {
    width: 100%;
    margin-bottom: 20px;
}

.fixture-page-container .events-container .top-box {
    position: relative;
    height: 20px;
}

.fixture-page-container .events-container .top-box .line, .fixture-page-container .events-container .bottom-box .line {
    position: absolute;
    left: calc(50% - 0.5px);
    height: 100%;
    width: 1px;
    background-color: rgb(255, 255, 255, 1);
}

.fixture-page-container .events-container .top-box .circle {
    position: absolute;
    background-color: rgb(255, 255, 255, 1);
    top: 0;
    left: calc(50% - 4px);
    width: 8px;
    height: 8px;
    border-radius: 50%;
}

.fixture-page-container .events-container .bottom-box {
    position: relative;
    height: 20px;
}

.fixture-page-container .events-container .bottom-box .circle {
    position: absolute;
    background-color: rgb(255, 255, 255, 1);
    bottom: 0;
    left: calc(50% - 4px);
    width: 8px;
    height: 8px;
    border-radius: 50%;
}

.fixture-page-container .events-container .event-box {
    height: 60px;
    
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
}

.fixture-page-container .events-container .event-box .content.left {
    width: calc(50% - 40px);
    justify-content: flex-start;
    flex-direction: row-reverse;
}

.fixture-page-container .events-container .event-box .center {
    position: relative;
    width: 80px;
}

.fixture-page-container .events-container .event-box .center .line {
    position: absolute;
    left: calc(50% - 0.5px);
    width: 100%;
    background-color: white;
    height: 100%;
    width: 1px;
}

.fixture-page-container .events-container .event-box .center .circle {
    position: absolute;
    left: calc(50% - 15px);
    top: calc(50% - 15px);
    width: 30px;
    height: 30px;
    background-color: rgb(22, 22, 33);
    color: rgb(255, 255, 255, 1);
    border: 1px solid white;
    border-radius: 50%;
    font-size: 14px;
    font-weight: 400;

    display: flex;
    justify-content: center;
    align-items: center;
}

.fixture-page-container .events-container .event-box .content.right {
    width: calc(50% - 40px);
    justify-content: flex-start;
    flex-direction: row;
}

.fixture-page-container .events-container .event-box .content {
    display: flex;
    align-items: stretch;
}

.fixture-page-container .events-container .event-box .content .icon {
    height: 100%;
    color: white;
    font-size: 14px;

    display: flex;
    justify-content: center;
    align-items: center;
}

.fixture-page-container .events-container .event-box .content .icon .card {
    width: 8px;
    height: 12px;
}

.fixture-page-container .events-container .event-box .content .icon .var-icon {
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 600;
    color: rgb(255, 255, 255, 0.7);
}

.fixture-page-container .events-container .event-box .content .text {
    display: flex;
    font-size: 13px;
    flex-direction: column;
    justify-content: center;
}

.fixture-page-container .events-container .event-box .content .text span:first-of-type {
    color: rgb(255, 255, 255, 1);
    font-weight: 400;
}

.fixture-page-container .events-container .event-box .content .text span:last-of-type {
    margin-top: 4px;
    color: rgb(255, 255, 255, 0.3);
    font-weight: 300;
}

.fixture-page-container .events-container .event-box .content.left .text {
    text-align: right;
    margin-right: 15px;
}

.fixture-page-container .events-container .event-box .content.right .text {
    text-align: left;
    margin-left: 15px;
}

@media (max-width:540px) {
    .fixture-page-container .empty-events {
        width: calc(100% - 20px);
    }
}